import React from 'react';
import './home.css';
import Img from '../img/kopi-academy.svg';

function Home() {
  return (
    <div>
        <div className="content">
            <div className="textBox">
                <h2>Kopi <span>Academy</span></h2>
                <p>
                  Jumpstart your career in the coffee industry with the Kopi Academy. We are on the lookout for brilliant coffee minds to embark on a complete coffee education program.
                </p>
                <a href="https://kopitoken.com">Learn More</a>
            </div>
            <div className="imgBox">
                <img src={Img} alt="imgbox"/>
            </div>
        </div>
    </div>
  );
}

export default Home;
